<template>
  <div class="zhuye">
    <div class="img">
      <img src="https://z3.ax1x.com/2021/06/30/RDF0RH.png" />
    </div>
    <div class="main">
      <div class="middle">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="姓名" label-width="80px" prop="student_name">
            <el-input v-model="form.student_name" placeholder="请输入姓名"></el-input>
          </el-form-item>
          <el-form-item label="学号" label-width="80px" prop="student_num">
            <el-input v-model="form.student_num" placeholder="请输入学号"></el-input>
          </el-form-item>
          <el-form-item label="班级" label-width="80px" prop="student_class">
            <el-input
              v-model="form.student_class"
              placeholder="请输入班级"
            ></el-input>
          </el-form-item>
          <el-form-item label="实验名称" label-width="80px" prop="experiment_name">
            <el-select v-model="form.experiment_name" placeholder="请选择实验名称" style="width:100%">
              <el-option label="劈尖测细丝实验" value="劈尖测细丝实验"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="text-align:center">
            <el-button class="btn" style="text-align:center" type="primary" @click="login()"
              >进入测试</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        student_name: "",
        student_num: "",
        student_class: "",
        experiment_name: "",
      },
      rules: {
        student_name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 19, message: "请正确输入姓名", trigger: "blur" },
        ],
        student_num: [
          { required: true, message: "请输入学号", trigger: "blur" },
          { min: 11, max: 11, message: "请输入正确的学号", trigger: "blur" },
        ],
        student_class: [{ required: true, message: "请输入班级", trigger: "blur" }],
        experiment_name: [
          { required: true, message: "请选择实验名称", trigger: "blur" },
        ],
      },

      checked: "",
    };
  },
  methods: {
    login() {
      this.$refs.form.validate(async(valide) => {
        if (!valide) return;
        const {data:res} = await this.$http.post('api/experiment/student',this.form)
        console.log(res);
        if(res.code == 200) {
          window.sessionStorage.setItem('student_id',res.data[0].id)
          this.$router.push('/test');
        }
      });
    },
  },
};
</script>

<style scoped>
.zhuye {
  height: 100%;
  background-color: #dee5f3;
}
.main {
  height: 45%;
  width: 30%;
  min-width: 300px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.middle {
  width: 80%;
  height: 70%;
  min-width: 250px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}
.btn {
  background-color: #6f8ed3;
  text-align: center;
}
.img {
  height: 60px;
  background-color: #4168c0;
}
</style>